<template>
  <div class="below">
    <div class="below_item">
      <div class="item_children" @click="_changePages(1)">
        <img :src="switchID==1?image.order:image.orderNo" alt="">
        <div class="children_tetx" :style="switchID==1?'color:#518EDE':''">订单中心</div>
      </div>
      <div class="item_children" @click="_changePages(2)">
        <img :src="switchID==2?image.user:image.userNo" alt="">
        <div class="children_tetx" :style="switchID==2?'color:#518EDE':''">用户中心</div>
      </div>
      <div class="item_children" @click="_changePages(3)">
        <img :src="switchID==3?image.money:image.moneyNo" alt="">
        <div class="children_tetx" :style="switchID==3?'color:#518EDE':''">财务中心</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name:'below',
  props:{
    pageID:{
      type:Number,
      default:1
    },
    changePage:{
      type:Function,
      default:()=>{}
    }
  },
  data(){
    return {
      switchID:this.pageID,
      image:{
        order:require('@/assets/images/index/order.png'),
        orderNo:require('@/assets/images/index/orderNo.png'),
        user:require('@/assets/images/index/user.png'),
        userNo:require('@/assets/images/index/userNo.png'),
        money:require('@/assets/images/index/money.png'),
        moneyNo:require('@/assets/images/index/moneyNo.png'),
      }
    }
  },
  created(){
    
  },
  methods:{
    _changePages(id){
      this.switchID=id
      this.changePage(id)
    }
  }
}
</script>

<style lang="scss">
@import './index.scss';
</style>


