<template>
  <div class="user">
    <div class="user_top" :style="background">
      <div class="top_left">
        <template v-if="userDefault">
          <img src="@/assets/images/userDefault.png" alt="" srcset="" />
        </template>
        <template v-else>
          <img src="@/assets/images/userDefault.png" alt="" srcset="" />
        </template>
      </div>
      <div class="top_right">Hi，张三</div>
    </div>
    <div class="user_box">
      <div class="box_item">
        <div class="item_one">总收益(元)</div>
        <div class="item_two">2966.00</div>
        <div class="item_three">
          <div class="three_box">
            <div>已到账(元)</div>
            <div class="box_fl">900.00</div>
          </div>
          <div class="three_line"></div>
          <div class="three_box">
            <div>在路上(元)</div>
            <div class="box_fl">2066.00</div>
          </div>
        </div>
        <div class="item_three">
          <div class="three_box">
            <div>已提现(元)</div>
            <div class="box_fl">900.00</div>
          </div>
          <div class="three_line"></div>
          <div class="three_box">
            <div>可提现(元)</div>
            <div class="box_flColor">2066.00</div>
          </div>
        </div>
        <div class="item_btn">去提现</div>
      </div>
      <div class="user_children navigator_arrow" @click="_changePageDetailed">
        <img src="@/assets/images/userProfit.png" alt="" srcset="" />
        收支明细
      </div>
    </div>
  </div>
</template>

<script>
export default {
    name:'user',
    data(){
      return {
        userDefault:true,
        background:{
            backgroundImage:'url(' + require('@/assets/images/userbackGround.png') + ')',
            backgroundRepeat:'no-repeat',
            backgroundSize: '100% 100%'
        }
      }
    },
    methods:{
      _changePageDetailed(){
        this.$router.push({ path:'/detailed'})
      }
    }
    
}
</script>

<style lang="scss">
    @import './index.scss';
</style>


