<template>
  <div class="index" :style="item">
       <component v-bind:is="'page'+switchId"></component>
    <div class="index_floot">
      <Below :pageID="switchId" :changePage="_changeId"></Below>
    </div>
  </div>
</template>

<script>
import Below from '@/components/below/Index.vue';
import MyOrder from '@/components/MyOrder/Index.vue';
import User from '@/components/userCenter/Index.vue';
import partnerCen from '@/components/partnerCen/Index.vue';
export default {
    name:'index',
    components:{
      "Below":Below,
      "page1":MyOrder,
      "page2":partnerCen,
      "page3":User
    },
    data(){
        return {
            switchId:1,
            item:{height:'',width:''}
        }
    },
    created() {
    this.item = {
      height: document.documentElement.clientHeight + "px",
      width: document.documentElement.clientWidth + "px"
    };
  },
  methods:{
      _changeId(id){
      const { switchId } = this
      if(switchId==id) return;
      this.switchId=id;
    },
  }
}
</script>

<style lang="scss">
@import './index.scss';
</style>


