import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index/Index'

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path: '/',//首页
      name: 'index',
      component: Index,
      meta:{index:1,keepAlive: true }
    },
    {
      path: '/orderState',//订单状态
      name: 'orderState',
      component: ()=>import('@/pages/orderState/Index'),
      meta:{index:2,keepAlive: false }
    },
    {
      path: '/detailed',//明显
      name: 'detailed',
      component: ()=>import('@/pages/detailed/Index'),
      meta:{index:2,keepAlive: false }
    },
  ]
})



