<template>
  <div class="order">
        <div class="order_top">订单中心</div>
        <!-- 导航栏 start -->
        <div class="order_line">
            <div class="line_item" 
            :class="listIndex==item.id?'line_children':''"
            v-for="(item,index) in listViewTop" 
            :key="index"
            @click="_changeSwitchId(item.id)"
            >
            <div class="line_item_text">{{item.title}}</div>
            <div :style="listIndex==item.id?'opacity:1':'opacity:0'" class="line_childrend"></div>
            </div>
        </div>
        <!-- 导航栏 end -->
        <!-- 列表 -->
        <div class="order_childred">
            <template v-if="_getListView"></template>
            <div class="childred_item" 
            v-for="(list,listIndex) in orderListView" 
            :key="listIndex"
            @click="_changePageProgress(list.id)"
            >
                <div class="item_top">
                    <div :class="list.state==1?'item_topColor':''">{{_setTextView(list.state)}}</div>
                    <div class="top_law">
                        <span>律师：{{list.name}}</span>
                        <img src="@/assets/images/work/tel.png" alt="">
                    </div>
                </div>
                <div class="item_cen">
                    <div class="cen_one">
                        <div class="one_left">
                            <img :src="list.img" alt="" srcset="" />
                            <div class="left_box">
                                <div class="box_one">律师服务</div>
                                <div class="box_two">规格</div>
                                <div class="box_three">￥{{list.money}}</div>
                            </div>
                        </div>
                        <div class="one_right">×{{list.num}}</div>
                    </div>
                    <div class="cen_two">共<span class="span1">{{list.num}}</span>件商品，合计：<span class="span2">￥{{list.money*list.num}}</span> </div>
                </div>
                <div class="item_fl">
                    <div class="fl_left">{{list.time}}</div>
                    <div class="fl_rightBox">
                        <!-- <div class="right_btn" v-show="list.state==2">催单</div> -->
                        <!-- 待付款 -->
                        <!-- <template v-if="list.state==1">
                            <div class="rightBox_null centerBox centerText">取消订单</div>
                            <div class="rightBox_null2 centerBox">去支付</div>
                        </template> -->
                        <!-- 处理中 -->
                        <!-- <template v-if="list.state==3">
                            <div class="rightBox_null centerBox centerText">催单</div>
                            <div class="rightBox_null3 centerBox" @click="_changePageProgress(item.id)">查看进度</div>
                        </template> -->
                        <!-- 已完结 -->
                        <!-- <template v-if="list.state==4"> -->
                            <!-- <div class="rightBox_null centerBox centerText">删除订单</div> -->
                            <div class="rightBox_null3 centerBox">查看进度</div>
                        <!-- </template> -->
                    </div>
                </div>
            </div>
            <div class="content_null" v-show="!_getListView">
                <img src="@/assets/images/work/NoCase.png" alt="" />
                <div class="null_text">暂无订单~</div>
            </div>
        </div>
      </div>
</template>

<script>
export default {
    name:'order',
    data(){
        return{
            listIndex:1,
            listViewTop:[
                {id:1,title:'全部'},
                {id:2,title:'个人订单'},
                {id:3,title:'下级用户订单'},
            ],
            orderListView:[//state 1待付款 2部分/全额付款 3处理中 4已完结
                {id:1,state:1,name:'张三',time:'2020.05.30 13:13', money:500,num:1,img:'https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fphoto.tuchong.com%2F3770578%2Ff%2F508481367.jpg&refer=http%3A%2F%2Fphoto.tuchong.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1629713482&t=412bb8d188f117aa321b7cac1f0923b4'},
                {id:2,state:2,name:'张三',time:'2020.05.30 13:13', money:11,num:4,img:'https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fphoto.tuchong.com%2F3770578%2Ff%2F508481367.jpg&refer=http%3A%2F%2Fphoto.tuchong.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1629713482&t=412bb8d188f117aa321b7cac1f0923b4'},
                {id:3,state:3,name:'张三',time:'2020.05.30 13:13', money:22,num:6,img:'https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fphoto.tuchong.com%2F3770578%2Ff%2F508481367.jpg&refer=http%3A%2F%2Fphoto.tuchong.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1629713482&t=412bb8d188f117aa321b7cac1f0923b4'},
                {id:4,state:4,name:'张三',time:'2020.05.30 13:13', money:33,num:7,img:'https://gimg2.baidu.com/image_search/src=http%3A%2F%2Fphoto.tuchong.com%2F3770578%2Ff%2F508481367.jpg&refer=http%3A%2F%2Fphoto.tuchong.com&app=2002&size=f9999,10000&q=a80&n=0&g=0n&fmt=jpeg?sec=1629713482&t=412bb8d188f117aa321b7cac1f0923b4'}
            ]
        }
    },
    computed: {
        _getListView:function(){
        return this.orderListView.length
        },
    },
    created(){
    },
    methods:{
        _changeSwitchId(switchId){
            this.listIndex=switchId
        },
        _setTextView(state){
            if(state==1) return '待付款';
            if(state==2) return '部分/全额付款';
            if(state==3) return '处理中';
            if(state==4) return '已完结';
        },
        _changePageProgress(id){//查看进度
            this.$router.push({path:'/orderState',query:{viewID:id}});
        }
    }
}
</script>

<style lang="scss">
@import './index.scss';

</style>


