<template>
  <div id="app">
    <transition :name="'fade-'+(direction==='forward'?'last':'next')">
        <keep-alive>
          <router-view v-if="$route.meta.keepAlive"></router-view>
        </keep-alive>
    </transition>
    <transition :name="'fade-'+(direction==='forward'?'last':'next')">
      <router-view v-show="!$route.meta.keepAlive"></router-view>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'App',
  data: () => {
    return {
      direction: ""
    };
  },
   watch: {
    $route(to, from) {
      let toName = to.name;
      const toIndex = to.meta.index;
      const fromIndex = from.meta.index;
      this.direction = toIndex < fromIndex ? "forward" : "";
    }
  }
}
</script>

<style scoped>
@supports (padding-bottom: env(safe-area-inset-bottom)) {
    #app {
      padding-bottom: env(safe-area-inset-bottom);
      padding-bottom: constant( safe-area-inset-bottom);
    }
  }
  .fade-last-enter-active {
  animation: bounce-in 0.6s;
}
.fade-next-enter-active {
  animation: bounce-out 0.6s;
}
@keyframes bounce-in {
  0% {
    transform: translateX(-100%);
  }
  100% {
    transform: translateX(0rem);
  }
}
@keyframes bounce-out {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0rem);
  }
}
</style>



