<template>
  <div class="goback" :style="color?'background:#3D7BE1':''">
      <img v-if="color" src="@/assets/images/backLeft.png" 
      alt="" 
      @click="()=>this.$router.go(-1)"
      />
      <img v-else src="@/assets/images/goback.png" alt="" srcset="" @click="()=>this.$router.go(-1)" />
      <div class="goback_text" :style="color?'color:#ffffff':''">{{title}}</div>
  </div>
</template>

<script>
export default {
    name:'goback',
    props:{
        title:{
            type:String,
            default:''
        },
        color:{
            type:Boolean,
            default:false
        }
    },
    data(){
        return{

        }
    },
}
</script>

<style lang="scss">
@import './index.scss';

</style>


